<style lang="scss" scoped>
.dialog_pram {
  margin-bottom: 10px;
  display: block;
}
.version_computer{
  display: flex;
  justify-content: center;
  >span{
    margin-right: 5px;
  }
}
.version_brage{
  display: flex;
  justify-content: flex-end;
  margin-bottom: 10px;
  font-size: 12px;
  align-items: center;
  color: #666;
  >button{
    margin: 0 5px 0 15px;
  }
}
