<template>
  <div class="app-container">
    <el-dialog
        title="选择文件"
        :visible.sync="addShow"
        width="500px"
        :close-on-click-modal='false'
        >
        <el-form ref="form" label-width="80px">
            <el-form-item label="版本号">
                <el-input clearable class="dialog_pram" placeholder="请输入版本号" v-model="uploadData.version" style="width: 200px;" />
            </el-form-item>
            <el-form-item label="文件名">
                <el-input clearable class="dialog_pram" placeholder="请输入文件名" v-model="uploadData.serverFileName" style="width: 200px;" />
            </el-form-item>
            <el-form-item label="创建者">
                <el-input clearable class="dialog_pram" placeholder="请输入创建者" v-model="uploadData.createdBy" style="width: 200px;" />
            </el-form-item>
            <el-form-item label="更新说明">
                <el-input clearable type="textarea" :rows="2" class="dialog_pram" placeholder="请输入说明" v-model="uploadData.explain" style="width: 200px;" />
            </el-form-item>
            <el-form-item label="上传文件">
                <el-upload
                        ref='upload'
                        drag
                        :auto-upload="false"
                        :data="uploadData"
                        name='file'
                        :file-list="fileList"
                        :action="baseUrl+uploadUrl"
                        :before-upload="beforeAvatarUpload"
                        :on-success="successUpload"
                    >
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                </el-upload>
            </el-form-item>
        </el-form>   
        <span slot="footer">
                <el-button  type="primary"  @click="submitUpload">确定</el-button>
                <el-button  type=""  @click="cancelUpload">取消</el-button>
        </span>
    </el-dialog>
    <div class="filter-container">
        <el-input clearable placeholder="请输入编号" v-model="searchData.id" style="width: 200px;" class="filter-item"/>
        <el-input clearable placeholder="请输入创建者" v-model="searchData.createdBy" style="width: 200px;" class="filter-item"/>
        <div class="filter-item" style="margin-right:20px">
             <el-date-picker
                clearable
                placeholder="请输入时间范围"
                @change="timeChange"
                v-model="searchTime"
                type="daterange"
                start-placeholder="开始日期"
                end-placeholder="结束日期">
            </el-date-picker>
        </div>
        <el-button class="filter-item" type="primary" icon="el-icon-search" @click="search">查询</el-button>
        <el-button class="filter-item" type="success" icon="el-icon-edit" @click="updateData(0)">新增版本记录</el-button>
    </div>

    

    <el-row class="version_brage">
      <el-button size="mini"  ></el-button>可回退到当前版本
      <el-button size="mini" type="primary" ></el-button>可使用当前版本
      <el-button size="mini" type="success" ></el-button>当前版本正在使用中
    </el-row>

    <el-table
      v-loading="listLoading"
      :data="dataList"
      :stripe="true"
      border
      fit
      highlight-current-row
      style="width: 100%;"
    >
      <el-table-column width="220" label="编号" prop="id" align="center" />
      <el-table-column  label="文件名" prop="serverFileName" align="center" />
      <el-table-column width="100" label="版本" prop="version" align="center">
        <template slot-scope="scope">
          <el-tag type="info">{{ scope.row.version }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="说明" prop="explain" align="center" />
      <el-table-column width="100" label="创建者" prop="createdBy" align="center" />
      <el-table-column min-width="250" label="主机管理" prop="" align="center" >
        <template slot-scope="scope">
            <!-- 灰色：版本回退 蓝色：版本使用 绿色:版本使用中-->
            <!-- serverStatus 0未上传 1已上传 2上传失败 3已回滚 -->
            <div class="version_computer">
              <template v-for="(item,index) in scope.row.servers">
              <el-popover
                v-if="item.serverStatus==0" :key="index"
                placement="top-start"
                title="提示"
                width="200"
                trigger="hover"
                content="在主机上运行当前版本">
                <el-button slot="reference" type='primary' size='mini' @click="uploadVersion(scope.row.id,item.id)">{{item.serverName}}</el-button>
              </el-popover>

              <el-popover
                v-if="(item.serverStatus==1 || item.serverStatus==3) & scope.row.version==item.serverVersion" :key="index"
                placement="top-start"
                title="提示"
                width="200"
                trigger="hover"
                content="当前版本运行中">
                <el-button slot="reference" type='success' size='mini' >{{item.serverName}}</el-button>
              </el-popover>

              <el-popover
                v-if="(item.serverStatus==1 || item.serverStatus==3) & scope.row.version!=item.serverVersion" :key="index"
                placement="top-start"
                title="提示"
                width="200"
                trigger="hover"
                content="回退到当前版本">
                <el-button slot="reference" type='mari' size='mini' @click="backVersion(scope.row.id,item.id)">{{item.serverName}}</el-button>
              </el-popover>
            </template>
            </div>
        </template>
      </el-table-column>
      <el-table-column width="150" label="操作" prop="" align="center" >
        <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="updateData(1,scope.row)">修改</el-button>
            <el-button type="warning" size="mini" @click="deleteData(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>


     <pagination v-show="totalNumber>0" :total="totalNumber" :page.sync="searchData.pageNum" :limit.sync="searchData.pageSize" @pagination="getList" />
  </div>
</template>

<script>
import { Version } from "@/api/version";
import Pagination from "@/components/Pagination";
export default {
  name: "version",
  components: { Pagination },
  data() {
    return {
      fileList: [],
      baseUrl: "",
      uploadUrl: "",
      addShow: false, //是否显示弹出框
      searchTime: [],
      //   搜索内容
      searchData: {
        pageNum: 1,
        pageSize: 10,
        id: null,
        createdBy: "",
        beginCreatedOn: "",
        endCreatedOn: ""
      },
      totalNumber: 0,
      // 上传的额外参数
      uploadData: {
        id: "",
        version: "",
        serverFileName:'',
        createdBy: "",
        explain: ""
      },
      listLoading: false,
      dataList: []
    };
  },
  created() {
    this.getList();
    this.baseUrl = Version.addVersion.baseUrl;
  },
  methods: {
    /**
     * @description 获取初始化数据
     * @function getList
     * @return {type} {description}
     */
    getList() {
      this.listLoading = true;
      Version.getVersion(this.searchData).then(response => {
        this.dataList = response.data.data;
        this.totalNumber = response.data.pageCount;
        this.listLoading = false;
      });
    },
    /**
     * @description 搜索数据
     * @function search
     */
    search() {
      this.getList();
    },
    /**
     * @description 搜索时间段更改
     * @function timeChange
     */
    timeChange(date) {
      this.searchData.beginCreatedOn = date ? date[0] : "";
      this.searchData.endCreatedOn = date ? date[1] : "";
    },
    updateData(type, row = {}) {
      this.addShow = true;
      this.fileList = [];
      if (type === 0) {
        this.uploadUrl = Version.addVersion.url;
        this.uploadData = {
          id: null,
          version: "",
          serverFileName:'',
          createdBy: "",
          explain: ""
        };
      } else {
        this.uploadData = {
          id: row.id,
          version: row.version,
          serverFileName:row.serverFileName,
          createdBy: row.createdBy,
          explain: row.explain
        };
        this.uploadUrl = Version.changeVersion.url;
      }
    },
    /**
     * @function submitUpload
     * @return {type} {description}
     */
    submitUpload() {
      if (!this.uploadData.version.trim()) {
        this.$message.warning("请输入版本号");
        return false;
      }
      if (!this.uploadData.serverFileName.trim()) {
        this.$message.warning("请输入文件名");
        return false;
      }
      if (!this.uploadData.createdBy.trim()) {
        this.$message.warning("请输入创建人");
        return false;
      }
      if (!this.uploadData.explain.trim()) {
        this.$message.warning("请输入更新说明");
        hasohaod;
        return false;
      }
      this.$refs.upload.submit();
    },
    cancelUpload() {
      this.uploadData = {
        id: "",
        createdBy: "",
        explain: ""
      };
      this.addShow = false;
    },
    /**
     * @description 删除版本
     * @function deleteData
     * @return {type} {description}
     */
    deleteData(id) {
      this.$confirm("确定删除该版本?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          Version.deleteVersion({
            id
          }).then(res => {
            this.$message({
              type: "success",
              message: "成功删除"
            });
            this.getList();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    /**
     * @function uploadVersion
     * @return {type} {description}
     */
    uploadVersion(navAllId, serverId) {
      this.$confirm("确定要使用该版本?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          Version.versionUpload({
            navAllId,
            serverId
          }).then(res => {
            this.$message({
              type: "success",
              message: "成功上传"
            });
            this.getList();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消上传"
          });
        });
    },
    /**
     * @function backVersion
     * @return {type} {description}
     */
    backVersion(navAllId, serverId) {
      this.$confirm("确定回退到该版本?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          Version.versionBack({
            navAllId,
            serverId
          }).then(res => {
            this.$message({
              type: "success",
              message: "成功回滚"
            });
            this.getList();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消回退"
          });
        });
    },
    /**
     * @description 文件上传前检测
     * @function beforeAvatarUpload
     * @param  {type} file 内置文件对象
     * @return {bollean} 是否满足当前设定额检测条件
     */
    beforeAvatarUpload(file) {
      const isJS = (file.type === "text/javascript" || file.type === "text/css");
      if (!isJS) {
        this.$message.error("只能上传js或者css文件");
      }
      return isJS;
    },
    /**
     * @description 成功后的回调参数
     * @function successUpload
     * @param  {type} response 成功后的毁掉参数
     */
    successUpload(response) {
      if (response.code === 200) {
        this.$message.success("成功");
        this.getList();
        this.addShow = false;
      }
    }
  }
};
</script>
